import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.section`
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  margin-top: 35px;
`;

const A_Tag = styled.a`
  display: flex;
  @media screen and (max-width: 767px) {
    flex-wrap: wrap;
  }
`;

const Text = styled.div`
  width: 80%;
  padding: 16px;
  order: 1;
  position: relative;
  .source-words,
  .source-website {
    color: rgba(0, 0, 0, 0.5);
  }
  .source-website {
    position: absolute;
    bottom: 16px;
    margin: 0;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    order: 2;
  }
`;
const Image = styled.div`
  width: 20%;
  padding-bottom: 20%;
  background: url("/static/img/chow.PNG") no-repeat center;
  background-size: cover;
  border-left: 1px solid #e0e0e0;
  order: 2;
  @media screen and (max-width: 767px) {
    width: 100%;
    padding-bottom: 50%;
    order: 1;
  }
`;

class Source extends Component {
  render() {
    return (
      <Div>
        <A_Tag>
          <Text>
            <h3 className="source-title">
              「周而復始 生生不息」 揉合傳統與現代之產品
            </h3>
            <p className="source-words">
              Design Thinking cannot begin without a deeper understanding of the
              people you are designing for. In order to gain those…
            </p>
            <p className="source-website">www.interaction-design.org</p>
          </Text>
          <Image />
        </A_Tag>
      </Div>
    );
  }
}

export default Source;
